.contact__item {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 1.4rem;
    margin-top: 1.4rem;

    border: 1px solid #62487A;
    border-radius: 10px;
    background-color: #f6f4f8;

    animation-duration: 2s;

    &--1 {
      animation-name: show;
    }

    &--2 {
      animation-name: showTwo;
    }
}

.edit__contact_item {
  padding-right: 150px;
  padding-left: 150px;
  flex-direction: column;

  & .contact__btns_container, .button {
    max-width: 100%;
    width: 100%;
    margin-right: 0 !important;
    margin-left: 0 !important;
  }
}

.contacts__info {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;

  @media (min-width: 360px) {
    flex-wrap: nowrap;
  }
}

.contact__btns_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-content: stretch;
  flex-grow: 1;

  & button {
    margin-left: 10px;
  }

  & button:first-child {
    margin-right: 10px;
  }
}

.contact__img_container {
    max-width: 100px;
    width: 100%;
    max-height: 70px;
    margin-right: 1.4rem;
    margin-bottom: 10px;

    border-radius: 10px;
    overflow: hidden;

    @media (min-width: 360px) {
      margin-bottom: 0;
    }
}

.strongText {
    font-weight: 700;
    margin-right: 10px;
}

@keyframes show {
    from {
      opacity: 0;
      transform: translateX(-300px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
} 

@keyframes showTwo {
  from {
    opacity: 0;
    transform: translateX(300px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
} 
