.add_form {
    width: 100%;
}

.addContact__btns {
    display: flex;
    align-items: center;
    width: fit-content;
    flex-direction: column;
    width: 100%;
    
    margin: 20px auto 0;

    @media (min-width: 1200px) {
        flex-wrap: nowrap;
    }

    & input[type=submit] {
        margin: 0;
        margin-right: 50px;
        margin-top: 20px;
    }

    & .button, input {
        max-width: 100%;
        width: 100%;
        margin-top: 20px;
        margin-right: 0 !important;
    }

   
}